.backdrop {
    position : fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.7);
    display: flex;
    justify-content: center;
    align-items: center;
}
.modal {
    background-color: var(--white);
    padding: 20px;
    border-radius: 5px;
    z-index: 1;
    max-height: 80vh;
    overflow-y: auto;
}