.add-product-heading {
    margin-top: 0;
    font-weight: 500;
    margin-bottom: 30px;
    font-size: 28px;
}

.add-product-container {
    padding: 10px;
    width: 25vw;
}

.add-product-form {
    display: flex;
    flex-direction: column;
}

.form-label {
    font-size: 16px;
    margin-bottom: 5px;
}

.form-input {
    padding: 8px 12px;
    font-size: 14px;
    border: 1px solid #ccc;
    border-radius: 4px;
    margin-bottom: 10px;
}

.submit-button {
    padding: 10px 15px;
    background-color: var(--yellow);
    color: var(--dark);
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
}